body.application {
    --bs-border-radius-xxl: 20px;
    --bs-border-radius-xl: 10px !important;
    --bs-primary-rgb: 121, 74, 255;
    --bs-dark-rgb: 41, 41, 41;
    .bg-secondary-2 {
        background: #f8f9fa;
    }
    .btn-text {
        font-weight: var(--tc-fw-bold);
    }
    .top-content {
        .line {
            border-left: 2px solid var(--tc-theme-primary);
            height: 14px;
        }
    }
    .btn-outline-dark {
        background:
            linear-gradient(var(--tc-system-white), var(--tc-system-white)) padding-box,
            (var(--tc-linear-4)) border-box !important;
        border: 1px solid transparent !important;
        --bs-btn-hover-color: var(--tc-system-black);
    }
    .btn-outline-secondary {
        background:
            linear-gradient(var(--tc-system-white), var(--tc-system-white)) padding-box,
            (var(--tc-linear-4)) border-box !important;
        border: 1px solid transparent !important;
        --bs-btn-hover-color: var(--tc-system-black);
    }
}
.application-home-section-1 {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
.bg-circle {
    .bg-circle-inner {
        width: 600px;
        height: 600px;
        border-radius: 300px;
        opacity: 0.4;
        background: var(--Gradient_2, linear-gradient(133deg, #2c2e3c 14.96%, rgba(255, 255, 255, 0) 84.63%));
    }
}
.application-home-section-3 {
    .card-feature {
        &:nth-child(odd) {
            @media (min-width: 1400px) {
                margin-top: 40px !important;
            }
        }
        &:hover {
            .bg-white {
                border-color: var(--tc-system-black) !important;
                transition: all 0.3s ease;
            }
            .icon-shape {
                background: var(--tc-system-black);
                border-color: var(--tc-system-black) !important;
                transition: all 0.3s ease;
                svg {
                    path {
                        fill: white;
                        transition: all 0.3s ease;
                    }
                }
            }
        }
    }
}
.application-home-section-4 {
    .card-process {
        border-bottom: 1px solid var(--tc-system-stroke);
        margin-bottom: 40px;
        padding-bottom: 40px;
        transition: all 0.3s ease-in-out;
        &:hover {
            border-bottom: 1px solid var(--tc-theme-primary);
        }
    }
    .bg-circle {
        .bg-circle-inner {
            width: 565px;
            height: 565px;
            border-radius: 565px;
            opacity: 0.4;
            background: var(--Gradient_2, linear-gradient(133deg, #2c2e3c 14.96%, rgba(255, 255, 255, 0) 84.63%));
        }
    }
}
.application-home-section-5 {
    .avatar-1 {
        width: 120px;
        height: 120px;
        border: 2px solid var(--tc-system-black);
    }
    .avatar-2 {
        width: 120px;
        height: 120px;
        border: 2px solid var(--tc-system-black);
        margin-left: -20px;
    }
}
.application-about-section-1 {
    .avatar-1 {
        width: 66px;
        height: 66px;
        border: 2px solid var(--tc-system-stroke);
    }
    .avatar-2 {
        width: 66px;
        height: 66px;
        border: 2px solid var(--tc-system-stroke);
        margin-left: -20px;
    }
}
.section-footer-16 {
    .icon {
        svg {
            height: 14px;
            width: 14px;
            fill: var(--tc-body-text-secondary);
            opacity: 0.5;
        }
        &:hover {
            svg {
                fill: var(--tc-system-black);
                opacity: 1;
                transition-duration: 0.3s;
            }
        }
    }
    .line-border {
        width: 1px;
        height: 300px;
        background: var(--tc-system-stroke);
    }
}
.rounded-bottom-80 {
    border-radius: 0px 0px 80px 80px;
}
.tag-journey {
    display: flex;
    width: 280px;
    flex-direction: column;
    align-items: flex-start;
    gap: 34px;
    .icon {
        display: flex;
        width: 100px;
        height: 100px;
        padding: 28px;
        justify-content: center;
        align-items: center;
        border-radius: 100px;
        background: var(--tc-system-white);
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.08);
        transition: all 0.25s linear;
    }
    &:hover {
        .icon {
            background: var(--tc-theme-primary);
            box-shadow: none;

            svg {
                path {
                    fill: var(--tc-system-white) !important;
                }
            }
        }
    }
}
.application-features-section-2 {
    .bg-light-5 {
        background-color: #eeecf4;
    }
    .icon-shape {
        background-color: rgba(121, 74, 255, 0.1);
    }
}
.blog-testimonials {
    --bs-border-color: var(--tc-theme-primary);
}
.pagination_item {
    background: var(--tc-bg-light);
    color: var(--tc-theme-primary);
    border-radius: 8px !important;
    font-size: 20px !important;
    font-weight: 600 !important;
    svg {
        path {
            stroke: var(--tc-theme-primary) !important;
            transition: 0.3s ease-in-out;
        }
    }
    &:hover {
        background: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:focus {
        box-shadow: none;
        background-color: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &.active {
        background: var(--tc-theme-primary) !important;
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
}
.swiper-pagination {
    margin-bottom: 120px;
    margin-left: 250px;
    .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
        background: var(--tc-system-white);
        opacity: 1;
    }
    .swiper-pagination-bullet-active {
        width: 10px;
        height: 10px;
        background: var(--tc-theme-primary);
        position: relative;
        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 18px;
            height: 18px;
            z-index: -1;
            border-radius: 10px;
            background: rgba(255, 255, 255, 0.4);
        }
    }
}
.application-how-it-work-section-2 {
    .blog-avatar {
        .icon-shape {
            &:not(:first-child) {
                margin-left: -18px;
            }
        }
    }
}
.accordion {
    .card {
        transition: all 0.35s ease-in-out;
        .card-header {
            background-color: transparent;
            a {
                .arrow {
                    width: 24px;
                    height: 24px;
                    display: inline-flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 50%;
                    background-color: var(--tc-system-white);
                    transition: 0.35s;
                    transform: rotate(0deg);
                    &::before {
                        font-family: bootstrap-icons;
                        content: '\F282';
                        color: var(--tc-system-black);
                        font-size: 18px;
                    }
                }
                &.collapsed {
                    .arrow {
                        transform: rotate(180deg);
                        transition: 0.35s;
                        &::before {
                            color: var(--tc-system-black);
                            content: '\F282';
                        }
                    }
                }
            }
        }
    }
}
.application-contact-section-1 {
    .form-group-item {
        @media (min-width: 1400px) {
            margin-top: -230px;
        }
    }
    .form-group {
        border-radius: 16px;
        border: 1px solid var(--tc-system-stroke);
        background: var(--tc-system-white);
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.08);
        .form-control {
            color: var(--tc-system-black);
            border-radius: 8px;
            border: 1px solid rgba(17, 17, 17, 0.1);
            background: rgba(255, 255, 255, 0.05);
        }
        textarea {
            min-height: 170px;
        }
        .form-select {
            background-color: rgba(255, 255, 255, 0.05);
            border: 1px solid rgba(17, 17, 17, 0.1);
            border-radius: 8px;
            &:focus {
                outline: none;
                box-shadow: none;
            }
        }
    }
}
